@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
.header {
    display: flex;
    justify-content: center;
    margin-bottom: 19px;
}

.header div {
    width: 65px;
    height: 35px;
    text-align: center;
    color: #D9D9D9;
}

.header div:first-child{
    margin-right: 16px;
}

Button {
    background-color: white;
    border: none;
    cursor: pointer;
    border-bottom: 2px solid #D9D9D9;
    padding-bottom: 8px;
    outline-color: #1890FF;
}

.chosen {
    border-bottom: 3px solid #1890FF;
    color: #1890FF;
}


.card {
    display: flex;
    box-shadow: 2px 3px 8px #D9D9D9;
    flex-direction: column;
}

.cardTop {
    display: flex;
}

.lowRes {
    display: none;
}

.highRes {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.movieInfo {
    margin: 12px 9px 2px 20px;
    display: flex;
    flex-direction: column;
}

.movieTitle {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 7px;
}

.movieTitle span {
    font-size: 20px;
    line-height: 26px;
}

.movieRating {
    flex-basis: 30px;
    flex-shrink: 0;
    min-height: 30px;
    max-height: 30px;
    border: solid 2px #E9D100;
    border-radius: 50%;
}

.movieRating p {
    font-size: 12px;
    font-weight: bold;
    line-height: 25px;
    text-align: center;
    margin: 0;
}

.date {
    font-size: 12px;
    color: #827E7E;
    margin-bottom: 7px;
}

.movieGenres {
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
}

.movieSinops {
    max-height: 130px;
}

.movieSinops span {
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
    overflow-y: hidden;
}

.highRes .stars {
    max-width: 240px;
    margin-top: auto;
}

svg {
    width: 0.75em;
    height: 0.75em;
}

.genre {
    font-size: 10px;
    color: rgba(0, 0, 0, 0.65);
    padding: 4px;
    border: solid 1px #D9D9D9;
    border-radius: 2px;
    margin-right: 8px;
    margin-bottom: 4px;
    background-color: #FAFAFA;
}

@media screen and (max-width: 1075px) {
    .moviePoster img {
        width: 60px;
    }
    .movieInfo {
        margin: 4px 9px 2px 12px;
    }

    .cardTop {
        display: grid;
        grid-template-columns: 60px 1fr;
    }

    .movieSinops {
        padding: 0 5px;
    }

    .lowRes {
        display: block;
    }

    .highRes {
        display: none;
    }
}
.movieslist {
    margin: 20px 0;
    display: grid;
    grid-template-columns: 454px 454px;
    grid-gap: 35px;
    gap: 35px;
}

@media screen and (max-width: 1075px) {
    .movieslist  {
        grid-template-columns: 1fr;
    }
}
.wrapper {
    width: 1010px;
    margin: 0 auto;
    padding: 36px;
    background-color: #fff;
    box-sizing: border-box;
    position: relative;
}

.paginator {
    display: flex;
    justify-content: center;
}

.alert {
    margin-top: 20px;
}

.spin {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 1075px) {
    .wrapper {
        width: 420px;
        padding: 12px;
    }
}
body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #E5E5E5;
}


